<template>
  <div class="cont">
    <div class="top">
    	<div class="header">
    		<span class="mo" v-if="item&&item.top">置顶</span>
    		<span class="mo" v-else-if="item&&item.good">精华</span>
    		<span v-else></span>
    		{{item&&item.title}}
    	</div>
    	<div class="middle">
    		<div>
    			<span>•发布于{{item&&item.create_at}}</span>
    			<span>•作者{{item&&item.author.loginname}}</span>
    			<span>•{{item&&item.visit_count}}次浏览</span>
    			<span>•来自{{item&&tabs[item.tab]}}</span>
    		</div>
    	</div>
    	<div class="text" v-html="item&&item.content">
    	</div>
    	<div class="reply">
    		<div class="lis1">
    				{{item&&item.reply_count}}回复
    			</div>
    		<ul v-for="reply in item&&item.replies">
    			
    			<li class="lis2">
    				<img :src="reply&&reply.author.avatar_url" alt="">
						<span class="s1">{{reply&&reply.author.loginname}}</span>
						<span class="s2">{{reply&&reply.create_at}}</span>
    			</li>
    			<li class="list3" v-html="reply&&reply.content"></li>
    		</ul>
    	</div>
    </div>
  </div>
</template>

<script>
import {getTopicData} from '@/getdata/getTopic.js';

import {processDate} from '@/getdata/getTopic.js';

export default {
  name: 'cont',
  data () {
    return {
      item:null,
      tabs:{
        ask:'问答',
        share:'分享',
        document:'文档翻译',
        experience:'实战经验',
        optimize:'优化策略'
      }
    }
  },
  mounted:function(){
		var id = this.$route.params.id
		getTopicData(id).then((response)=>{
			response.data.data.create_at = processDate(response.data.data.create_at);
			for(var i=0;i<response.data.data.replies.length;i++){
				response.data.data.replies[i].create_at=processDate(response.data.data.replies[i].create_at);
			}
			this.item = response.data.data;
		})
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
@import '../../assets/sass/reset.scss';
.cont{
	margin-bottom:rem(130px);
	.top{
		width:rem(710px);
    margin: 0 auto;
    .header{
			margin-top:rem(20px);
			font-size:rem(40px);
			font-weight:bold;
    	.mo{
				padding:rem(5px) rem(15px);
				background:#80bd01;
				border-radius: rem(5px);
				font-size:rem(30px);
    	}
    }
    .middle{
    	margin-top:rem(20px);
    	font-size:rem(26px);
    	color:#999;
    	border-bottom: rem(1px) solid #999;
    	div{
			margin-bottom: rem(20px);
    	}

    }
    .markdown-text{
        p{
            white-space: pre-wrap;
            word-break:break-word;
            img{
                width:100%;
            }
        }
    }
    .text{
    	width:rem(710px);
    	white-space: pre-wrap;
    	word-break:normal;
    	margin-top: rem(20px);
    	font-size:rem(40px);
    	p{
    		white-space: pre-wrap;
    		word-break:break-word;
    		a{
    			white-space: pre-wrap;
    			word-break:break-word;
    		}
    	}
    	h1,h2,h3,h4,h5,h6{
    		border-bottom: rem(1px) solid #999;
    	}
    	code{
				width:100%;
    		white-space: pre-wrap;
    		word-break:break-word;
    	}
    }
    .reply{
    	width:rem(710px);
    	.lis1{
					width:100%;
					height:rem(80px);
					background:#f6f6f6;
					line-height:rem(80px);
					font-size:rem(30px);
				}
				a{
    			white-space: pre-wrap;
    			word-break:break-word;
    		}
			ul{
				border-bottom: rem(1px) solid #999;
				.lis2{
					margin-top:rem(20px);
					width:100%;
					display:flex;
					img{
						width:rem(90px);
						height:rem(90px);
						display:inline-block;
					}
					.s2{
						color:#0088cc;
					}
					span{
						margin:0 rem(10px);
						font-size:rem(25px);
					}
				}
				.list3{
					font-size:rem(32px);
				}
			}
    }
	}
}

</style>


