</template>

<script>
export default {
  name: 'Index',
  data () {
    return {
      
    }
  },
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../assets/sass/reset.scss';
	.header{
		width:100%;
		height:rem(100px);
		background:#333;
		color:#fff;
		position:fixed;
		top:0;
		left:0;
		ul{
			display:flex;
			justify-content:space-around;
			align-items:center;
			height:rem(100px); 
			li{
				font-size:rem(33px);
				padding:rem(8px) rem(16px);
			}
			.router-link-exact-active{
				color:#80bd01;
			}
		}
	}
	.box{
		width:100%;
		height:rem(100px);
	}
</style>


