<template>
  <div class="index">
    <div class="top">我</div>
    <div class="form">
    	<form action="" id="frm">
	    	<h1>用户登录</h1>
	    	<div>
	    		<label for=""><span class="iconfont icon-people"></span></label>
	    		<input type="number" placeholder="请输入手机号" id="user">
	    		
	    	</div>
	    	<div class="divs">
	    		<span class="span" id="uMsg"></span>
	    	</div>
	    	
	    	<div>
	    		<label for=""><span class="iconfont icon-mima"></span></label>
	    		<input type="password" placeholder="请输入密码" id="pwd">
	    		
	    	</div>
	    	<div class="divs">
	    		<span class="span" id="pMsg"></span>
	    	</div>
	    	
	    	<div class="Box">
	    		<span>记住密码</span>
	    		<span class="s2">注册账号</span>
	    	</div>
	    	<div class="box">
	    		<button>登录</button>
	    	</div>
	    </form>
	    <p>------第三方直接登录------</p>
	    <div class="vbox">
	    	<span class="iconfont icon-weixin1"></span>	
	    	<span class="iconfont icon-zhifubao"></span>	
	    	<span class="iconfont icon-qq"></span>	
	    </div> 
    </div>
  </div>
  
</template>

<script>
export default {
  name: 'index',
  data () {
    return {
      
    }
  },
  mounted:function(){
  	var frm = document.getElementById('frm'),
			  user = document.getElementById('user'),
			  pwd = document.getElementById('pwd'),
		   	uMsg = document.getElementById('uMsg'),
			  pMsg = document.getElementById('pMsg');
       
          var uBool=false,
              pBool=false;
            // 1.判断用户名称
            user.onfocus = function(){
            	uMsg.innerHTML ='请输入4-20位的用户名'
            	// 去掉class 名称
            	uMsg.className = '';
            }
            user.onblur = function(){
            	//1. 获得 user中的值 清除两边空格
            	var val = this.value.trim();
            	//console.log(val);
            	var reg = /^1[34578]\d{9}$/;// 4-20位字母数字 下划线，- 中文
            	if(val.length==0){
                    uMsg.innerHTML = '用户名不能为空';
                   // 添加class 名称
                    uMsg.className ='err';
                    uBool = false;
            	}else if(reg.test(val)){
                   // 正确用户名称
                   uMsg.innerHTML = '用户名正确';
                   // 添加class 名称
                   uMsg.className ='succ';
                   uBool = true;
            	}else{
                	 uMsg.innerHTML ='用户名不正确';
                	 uMsg.className ='err';
                	 uBool = false;
            	}
            }
            // 判断密码
            pwd.onfocus = function(){
            	pMsg.innerHTML = '请输入6-15密码';
            	pMsg.className = '';
            }
            pwd.onblur = function(){
            	var val = this.value.trim();
            	var reg =/^[\w\-]{6,15}$/;
            	if(reg.test(val)){
                   pMsg.innerHTML = '密码正确';
                   pMsg.className ='succ';
                   pBool = true;
            	}else{
            	     pMsg.innerHTML = '密码不正确';
                   pMsg.className ='err';
                   pBool = false;
            	}
            }
            // 提交表单
            frm.onsubmit = function(){
            	/*if(用户名不合法 !false || 密码不合法 !false){
                return false; // 阻止表单提交
        	    }
                   !(uBool && pBool)
        	    */
            	if(!uBool || !pBool){
                   return false;
            	}
            }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../assets/sass/reset.scss';
	.index{
		.top{
			width:100%;
			height:rem(100px);
			background:#333;
			color:#fff;
			font-size:rem(40px);
			line-height:rem(100px);
			text-align:center;
		}
		.form{
			width:100%;
			padding-top:rem(200px);
			color:#333;
			h1{
				text-align:center;

			}
			div{
				margin:0 auto;
				width:rem(500px);
				margin-top:rem(20px);
				border:rem(1px) solid #ccc;

				label{
					.iconfont{
						font-size: rem(60px);

					}
				}
				input{
					width:rem(400px);
					height:rem(60px);
					border:0;
					padding:rem(11px) rem(15px);
					font-size:rem(30px);
				}
			}
			.divs{
				border:0;
			}
			.span{
				margin-left:rem(150px);
			}
			.succ{
				color:#0f0;
			}
			.err{
				color:#f00;
			}
			.Box{
				border:0;
				.s2{
					margin-left:rem(285px);
					color:#f93063;
				}
			}
			.box{
				border:0;
				button{
					width:rem(500px);
					height:rem(80px);
					background:#80bd01;
					color:#fff;
					font-size:rem(35px);
					border:0;

				}
			}
			p{
				text-align: center;
				color:#333;
				font-size:rem(30px);
				margin-top: rem(80px);
			}
			.vbox{
				width:rem(500px);
				margin:rem(30px) auto 0;
				border:0;
				.iconfont{
					font-size:rem(80px);
					margin-right:rem(120px);
				}
			}
		}
	}
</style>


