<template>
  <div class="list">
	<loading v-if="!items.length"></loading>
    <ul class="items">
    	<li class="item" v-for="item in items">
    		<img v-lazy="item.author.avatar_url" alt="">
    		<div class="tab">
    			<div class="mo" v-if="item.top">置顶</div>
    			<div class="mo" v-else-if="item.good">精华</div>
    			<div v-else>{{tabs[item.tab]}}</div>
    		</div>
    		<router-link :to=" '/index/cont/'+item.id" tag="div" class="title">{{item.title}}</router-link>
            <span>{{item.last_reply_at}}</span>
    	</li>
    </ul>
  </div>
</template>
<script>

//引入loading 组件
import loading from '@/components/Loading';

export default {
  name: 'List',
  data () {
    return {
        
      tabs:{
        ask:'问答',
        share:'分享',
        document:'文档翻译',
        experience:'实战经验',
        optimize:'优化策略'
      }
    }
  },
  props:['items'],
  components:{
    loading
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../../assets/sass/reset.scss';
// 主题列表的样式
ul.items{
    width:rem(710px);
    // height:rem(1000px);
    margin: 0 auto;
    
    .item{
        display: flex;
        align-items: center;
        height: rem(100px);
        border-bottom: rem(1px) solid #ccc;
        img{
            width: rem(60px);
            height: rem(60px);
            border-radius: rem(10px);
        }
        .tab{
            margin: 0 rem(20px);
            div{
                font-size: rem(22px);
                // width: rem(65px);
                height: rem(40px);
                padding:rem(5px) rem(20px);
                text-align: center;
                line-height: rem(40px);
                border-radius: rem(5px);
                background: #e5e5e5;
                color:#999;
            }   
            .mo{
                background: #80bd01;
                color:#fff;                 
            }
        }
        .title{
            flex:1;
            color:#333;
            font-size: rem(32px);
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
        }
    }
}
</style>


