import Vue from 'vue'
import App from './App'
import router from './router'
import VueLazyload from 'vue-lazyload'

//懒加载配置
Vue.use(VueLazyload, {
  preLoad: 1.3,
  //当你的图片请求失败用这个地方的图片
  error: require('../static/images/touxiang.jpg'),
  //当你的图片正在请求的时候用这个地方的图片
  loading: require('../static/images/txLoading.gif'),
  attempt: 1
})


Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



