<template>
  <div class="ask">
    <topic-list :items="items"></topic-list>
    <infinite-loading v-if="isShow" @infinite="infiniteHandler"></infinite-loading>
    
  </div>
</template>

<script>
//引入axios 模块 (封装的ajax)
import {getTopics} from '@/getdata/getTopic.js';
//引入分页滑动模块
import InfiniteLoading from 'vue-infinite-loading';
//引入axios 模块（封装的ajax）
import topicList from '@/components/topics/List';

import {processDate} from '@/getdata/getTopic.js';
export default {
  name: 'ask',
  data () {
    return {
      items:[],
      page:1,
      isShow:true
    }
  },
  //钩子函数
  mounted:function(){
    //ES6
    getTopics({tab:'ask',limit:20,page:1}).then((response)=>{
      for(var i=0;i<response.data.data.length;i++){
        response.data.data[i].last_reply_at=processDate(response.data.data[i].last_reply_at);
      }
      this.items = response.data.data;
    })
  },
  methods: {
    infiniteHandler($state) {
      
      setTimeout(()=>{
        //ES6查询全部主题数据
        getTopics({tab:'ask',limit:20,page:++this.page}).then((response)=>{
          for(var i=0;i<response.data.data.length;i++){
            response.data.data[i].last_reply_at=processDate(response.data.data[i].last_reply_at);
          }
          //把查询到的数据放到 items里
          this.items = this.items.concat(response.data.data);
          if(response.data.data.length==0){
            this.isShow = false;
            return;
          }
          $state.loaded();
        })

      },1000)
    },
  },
  components:{
    topicList,InfiniteLoading
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../../assets/sass/reset.scss';
.ask{
  margin-bottom:rem(100px);
}

</style>


