import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index'
import Gzh from '@/components/gzh'
import Me from '@/components/me'



//二级路由
import All from '@/components/topics/all'
import Ask from '@/components/topics/ask'
import Act from '@/components/topics/act'
import Good from '@/components/topics/good'
import Share from '@/components/topics/share'
import Word from '@/components/topics/word'
import Optimize from '@/components/topics/optimize'

import Cont from '@/components/topics/cont'



Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index',
      name: 'index',
      component: Index,
      children:[
      	{
      		path:'/',
      		component:All
      	},
      	{
      		path:'act',
      		component:Act
      	},
      	{
      		path:'ask',
      		component:Ask
      	},
      	{
      		path:'good',
      		component:Good
      	},
      	{
      		path:'share',
      		component:Share
      	},
      	{
      		path:'word',
      		component:Word
      	},
        {
          path:'optimize',
          component:Optimize
        },
        {
          path:'cont/:id',
          component:Cont
        }
      ]
    },
    {
      path: '/gzh',
      name: 'gzh',
      component: Gzh
    },
    {
      path: '/me',
      name: 'me',
      component: Me
    },
    
    {
    	path:'/*',
    	redirect:'/index'
    }
  ]
})



