<template>
  <div id="app">
    <div class="footer">
      <ul class="items">
        <router-link to="/index" tag="li" :class="resPath=='index'?'mo':''">
          <span class="iconfont icon-shouye"></span>
          <p>首页</p>
        </router-link>
        <router-link to="/gzh" tag="li" :class="resPath=='gzh'?'mo':''">
          <span class="iconfont icon-erweima"></span>
          <p>公众号</p>
        </router-link>
        <router-link to="/me" tag="li" :class="resPath=='me'?'mo':''">
          <span class="iconfont icon-people_fill"></span>
          <p>我</p>
        </router-link>
      </ul>
    </div>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  computed:{
    resPath:function(){
      var arr = this.$route.path.split('/');
      return arr[1];
    }
  }
}
</script>

<style lang="scss">
@import './assets/sass/reset.scss';
.footer{
  width:100%;
  height:rem(120px);
  background:#333;
  position:fixed;
  bottom:0;
  left:0;
  color:#fff;
  ul{
    display:flex;
    li{
      width:33.333333%;
      font-size:rem(34px);
      text-align:center;     
      height:rem(120px);
      .iconfont{
        font-size:rem(55px);
        margin-top:rem(10px);
        display:block;
      }
    }
    .mo{
      color:#80bd01;
    }
  }
  
}
</style>



